import bus from "@/bus.js";
import { mapState, mapGetters, mapMutations } from 'vuex'; //引入state，getters，mutations

export default {
  name: 'collect',
  props: {// download_tips_dia: Boolean
  },
  data: function data() {
    return {
      loading: false,
      loading_count: 0,
      origin_design_data: Array
    };
  },
  created: function created() {},
  mounted: function mounted() {},
  computed: {
    filter_collect_data: function filter_collect_data() {
      return this.origin_design_data;
    }
  },
  methods: {
    cout_load_img: function cout_load_img() {
      // 全部load完解除loading
      var me = this;
      this.loading_count += 1;

      if (this.loading_count >= this.origin_design_data.length) {
        this.loading = false;
      }
    },
    delete_collect_func: function delete_collect_func(id, level, index) {
      // 删除收藏模板
      var me = this;
      var is_user = 0;

      if (level == "permanent") {
        is_user = 2;
      } else {
        is_user = 1;
      }

      me.axios.get("/Collection/change_collection?template_id=".concat(id, "&is_user=").concat(is_user)).then(function (data) {
        console.log(data);
        me.origin_design_data.splice(index, 1);
        console.log(me.filter_collect_data);
        me.$message('取消成功');
      }).catch(function (e) {
        console.log(e);
      });
    },
    download_collection_func: function download_collection_func(path) {
      var me = this;
      bus.$emit('download', path);
    }
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      vm.axios.get("/Collection/find_collection").then(function (data) {
        console.log(data.data.data);
        vm.origin_design_data = data.data.data;

        if (data.data.data.length > 0) {
          vm.loading = true;
        } else {
          vm.loading = false;
        }
      }).catch(function (e) {
        console.log(e);
      });
    });
  }
};